<template>
  <b-modal size="lg" :lazy="true" id="modalbank" title="Bank Transactions">
    <file-upload url='http://axiomcredit-001-site1.etempurl.com/api/Applicant/2/BankTransuctions' btn-label="Add new Records" accept=".csv"  @change="onFileChange"></file-upload>


    <b-card >
      <b-row>
        <b-col sm="5">
          <h4 class="card-title mb-0">Bank Transactions</h4>
          <div class="small text-muted">2018</div>
        </b-col>


      </b-row>
      <chart :transactions="transactions" class="chart-wrapper" style="height:200px;margin-top:10px;" height="200"></chart>

    </b-card>
    {{file}}

    <div slot="modal-footer">

    </div>
  </b-modal>
</template>

<script>
    import Chart from '../components/BanktrasuctionChart'
    import FileUpload from 'v-file-upload'
    import { mapGetters } from 'vuex'
    export default {
      name: '',
      components: {Chart, FileUpload},
      data () {
        return {
          file: {}
        }
      },
      computed: {...mapGetters({transactions: '$_dashboard/transactions'})},
      methods: {
        onFileChange (file) {
          this.file = file
        }
      }
    }
</script>

<style scoped>

</style>



