import api from '../../../api'
import odata from '../../../api/odata'
export default {
  namespaced: true,
  state: {
    profile: {},
    offers: [],
    transactions: []
  },
  actions: {
    GET_OFFERS: (context) => {
      odata('Offers').expand('Supplier').get(function (data) {
        //  console.log(data)
        context.commit('OFFERS_UPDATED', data)
      })
    },
    GET_TRANSACTIONS: ({commit}) => {
      api.get('/Applicant/2/BankTransuctions').then(res => {
        commit('SET_TRANSACTIONS', res.data)
      })
    },
    GET_PROFILE: (context) => {
      api.get('/Applicant/2')
        .then((response) => {
          context.commit('PROFILE_UPDATED', response.data)
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.error(error)
        })
    },
    APPLY_CREDIT: ({commit}, offerID) => {
      console.log(offerID)
      api.post('/Applicant/2/Applications', {OfferId: offerID}).then()
        .catch((error) => {
          // eslint-disable-next-line
          console.error(error)
        })
    }},
  getters: {
    offers: state => state.offers,
    profile: state => state.profile,
    transactions: state => state.transactions,
    // current_score: state => state.profile.scores && state.profile.scores[0].score
    score_history: state => {
      if (state.profile.scores) {
        return state.profile.scores.map(s => s.score)
      }
    }
  },
  mutations: {
    PROFILE_UPDATED: (state, profile) => {
      profile.scores.sort((a, b) => new Date(a.date) - new Date(b.date))
      state.profile = profile
    },
    OFFERS_UPDATED: (state, offers) => {
      state.offers = offers
    },
    SET_TRANSACTIONS: (state, trans) => {
      state.transactions = trans
    }
  }
}



