<script>
import { Line } from 'vue-chartjs'

// const brandPrimary = '#20a8d8'
const brandSuccess = '#4dbd74'
// const brandInfo = '#63c2de'
const brandDanger = '#f86c6b'

export default {
  extends: Line,
  props: ['height', 'transactions'],
  mounted () {
    // var elements = 27
    const debit = []
    const credit = []
    const balance = []
    const lables = []

    // for (var i = 0; i <= elements; i++) {
    //   data1.push(random(50, 200))
    //   data2.push(random(80, 100))
    //   data3.push(65)
    // }
    this.transactions.forEach(t => {
      debit.push(t.debit)
      credit.push(t.credit)
      balance.push(t.balance)
      lables.push(t.month + '/' + t.year)
    })
    this.renderChart({
      labels: lables,
      datasets: [
        {
          label: 'Deposit',
          backgroundColor: 'transparent',
          borderColor: brandSuccess,
          pointHoverBackgroundColor: '#fff',
          borderWidth: 2,
          data: debit
        },
        {
          label: 'Withdrawal',
          backgroundColor: 'transparent',
          borderColor: brandDanger,
          pointHoverBackgroundColor: '#fff',
          borderWidth: 2,
          data: credit
        }
      ]
    }, {
      maintainAspectRatio: false,
      legend: {
        display: false
      },
      scales: {
        xAxes: [{
          gridLines: {
            drawOnChartArea: false
          }
        }],
        yAxes: [{
          ticks: {
            beginAtZero: true,
            maxTicksLimit: 5,
            stepSize: Math.ceil(20000 / 5),
            max: 20000
          },
          gridLines: {
            display: true
          }
        }]
      },
      elements: {
        point: {
          radius: 0,
          hitRadius: 10,
          hoverRadius: 4,
          hoverBorderWidth: 3
        }
      }
    })
  }
}
</script>



