<template>
  <b-card no-body class="bg-warning" :no-body="true">
    <b-card-body class="pb-0">
      <b-dropdown class="float-right" variant="transparent p-0" right>
        <template slot="button-content">
          <i class="icon-settings"></i>
        </template>
        <b-dropdown-item>Action</b-dropdown-item>
        <b-dropdown-item>Another action</b-dropdown-item>
        <b-dropdown-item>Something else here...</b-dropdown-item>
        <b-dropdown-item disabled>Disabled action</b-dropdown-item>
      </b-dropdown>
      <p>Current Score</p>
      <h2 class="mb-0">{{score_history && score_history[score_history.length-1]}}</h2>

    </b-card-body>
    <card-line3-chart-example v-if="score_history" :data="score_history" class="chart-wrapper" style="height:70px;" height="70"/>
  </b-card>
</template>

<script>
  import CardLine3ChartExample from './CardLine3ChartExample'
  export default {
    name: '',
    props: ['score_history'],
    components: {CardLine3ChartExample}
  }
</script>

<style scoped>

</style>



