<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col style="height: 100px" sm="6" lg="8">
        <b-card :no-body="true" header="Feeds">
          <div v-for="offer in offers">
          <b-card no-body :no-body="true"><b-card-body>
            <strong>{{offer.Title}}</strong>
             <p>{{offer.Description}}</p>
          </b-card-body></b-card>
  </div>
        </b-card>
      </b-col>
      <b-col sm="6" lg="4">
        <score-card :score_history="score_history"></score-card>

        <b-card no-body :no-body="true" >
          <b-card-body class="pb-0">
            <b-row>
              <b-col><b-button  v-b-tooltip.hover title="Bank Account"   variant="link" v-b-modal.modalbank><i class="fa fa-usd fa-2x"></i><p>{{profile.bankTransactionCount}}</p></b-button></b-col>
              <b-col><i title="Mobile Usage" class="fa fa-mobile fa-2x"></i><p>45</p></b-col>
              <b-col><i title="Purchases" class="fa fa-shopping-cart fa-2x"></i><p>250</p></b-col>
              <b-col><i title="Purchases" class="fa fa-shopping-cart fa-2x"></i><p>250</p></b-col>
            </b-row>
            <banktransuction-dialog></banktransuction-dialog>
          </b-card-body>
          <!--<ul class="list-inline">-->
          <!--<li class="list-inline-item"><i title="Bank Account" class="fa fa-usd fa-2x"></i><p>1950</p></li>-->
          <!--<li class="list-inline-item"><i title="Mobile Usage" class="fa fa-mobile fa-2x"></i><p>45</p></li>-->
          <!--<li class="list-inline-item"><i title="Mobile Usage" class="fa fa-mobile fa-2x"></i><p>45</p></li>-->
          <!--</ul>-->


        </b-card>

      </b-col>
    </b-row>
    <b-card >
      <b-row>
        <b-col sm="5">
          <h4 class="card-title mb-0">Credit Progress</h4>
          <div class="small text-muted">2018</div>
        </b-col>


      </b-row>
      <main-chart-example class="chart-wrapper" style="height:200px;margin-top:10px;" height="200"></main-chart-example>
      <div slot="header">
        <b-row>
          <b-col lg="8">
            <ul  class="list-inline">
              <li class="list-inline-item">
                <div class="text-muted">Returns</div>
                <strong>18(40%)</strong>
                <b-progress height={} class="progress-xs mt-2" :precision="1" variant="success" :value="40"></b-progress>
              </li>
              <li class="list-inline-item">
                <div class="text-muted">Defaults</div>
                <strong>24.093 (20%)</strong>
                <b-progress height={} class="progress-xs mt-2" :precision="1" variant="info" :value="20"></b-progress>
              </li>
              <li class="list-inline-item">
                <div class="text-muted">Active</div>
                <strong>78.706 (60%)</strong>
                <b-progress height={} class="progress-xs mt-2" :precision="1" variant="warning" :value="60"></b-progress>
              </li>

            </ul>
          </b-col>
          <b-col lg="4">
            <div class="text-muted">Current Balance</div>
            <strong>(-) 5750</strong>
            <b-progress height={} class="progress-xs mt-2" :precision="1" variant="success" :value="40"></b-progress>
          </b-col>
        </b-row>

      </div>
    </b-card>

  </div>
</template>

<script>
  import ScoreCard from '../../components/ScoreCard'
  import MainChartExample from './components/MainChartExample'
  import Profile from './model/Profile'
  import { mapGetters } from 'vuex'
  import store from './store'
  // import { makeDialog } from 'vue-modal-dialogs'
  import BanktransuctionDialog from './components/BanktrasuctionsDialog'
  // const bankDialog = makeDialog(BranktransuctionDialog)
  export default {
    name: 'dashboard',
    components: {ScoreCard, MainChartExample, BanktransuctionDialog},
    data () {
      return {
        profileX: new Profile()
      }
    },
    computed: {
      ...mapGetters({
        offers: '$_dashboard/offers',
        profile: '$_dashboard/profile',
        score_history: '$_dashboard/score_history'
      })
    },
    created () {
      this.$store.registerModule('$_dashboard', store)
    },
    methods: {
      showDialog () {
        console.log('showing')
        // bankDialog()
      }
    },
    mounted () {
      this.$store.dispatch('$_dashboard/GET_PROFILE')
      this.$store.dispatch('$_dashboard/GET_OFFERS')
      this.$store.dispatch('$_dashboard/GET_TRANSACTIONS')
    }
  }
</script>



